// ========================================================
//
// Fractal theme styles
//
// ========================================================

// Functions
@import "functions/_palette.scss";
@import "functions/_parse-int.scss";
@import "functions/_remify.scss";

// Config
@import "config/_variables.scss";

// Documentation styles
@import "docs/styleguide.scss";

// Theme overrides
a {
  color: $color-link;

  &:hover {
    color: $color-link--hover;
  }

  &:active {
    color: $color-link--active;
  }
}

.Pen-previewLink svg {
  fill: currentColor;
}

.Header {
  background-color: palette(primary, dark);
  color: palette(primary, light);
}
